<!-- eslint-disable max-len -->
<script lang="ts" setup>
import type { PxlIcon } from "~/components/U/Icon";
import { iconBind } from "~/components/U/Icon";

export type ITabProp = string | ITab;
export type ITabChildren = { key: string; label: string; tabs: ITabProp[] };
export interface ITab {
  key?: string;
  label: string;
  to?: string | null;
  icon?: PxlIcon;
  active?: boolean | null;
  disabled?: boolean | null;
  indicatorText?: string | null;
  indicatorType?: "danger" | "warning" | null;
  onClick?: (tab: ITabProp) => any;
  children?: ITabChildren;
  [key: string]: any;
}

const props = withDefaults(
  defineProps<{
    tabs?: ITabProp[];
    active?: string;
    filled?: boolean;
  }>(),
  {
    tabs: () => [],
  },
);
const emit = defineEmits(["update:active", "tabClick"]);

const tabs = computed<ITab[]>(() =>
  (props.tabs || []).map((t, tIndex) => {
    if (typeof t === "string" || typeof t === "number") {
      return { label: t, key: t };
    }
    if (!t.key)
      t.key = t.label || String(tIndex);
    return t;
  }),
);

function onTabClick(tab: ITab) {
  if (!tab.disabled) {
    tab.onClick && tab.onClick(tab);
    emit("tabClick", tab);
    emit("update:active", tab.key);
  }
}

function isActive(tab: ITab) {
  return props.active === tab.key || tab.active;
}
</script>

<template>
  <ul class="tabs-list" :class="[{ 'tabs-list-filled': props.filled }]">
    <li v-for="tab in tabs" :key="tab.key" class="whitespace-nowrap flex-1">
      <span
        v-if="tab.disabled"
        href="#"
        class="tab disabled"
        :class="{ active: isActive(tab), disabled: tab.disabled }"
        @click="onTabClick(tab)"
      >
        <slot :tab="tab" :active="isActive(tab)">
          <span class="relative">
            {{ tab.label }}
            <span v-if="tab.indicatorText" class="r-indicator" :class="`r-indicator--${tab.indicatorType || 'danger'}`">
              {{ tab.indicatorText }}
            </span>
          </span>
          <UIcon v-if="tab.icon" v-bind="iconBind(tab.icon)" />
        </slot>
      </span>

      <NuxtLink
        v-else-if="tab.to"
        :to="tab.to"
        class="tab"
        exact-active-class="active"
        :class="{ active: isActive(tab), disabled: tab.disabled }"
        @click="onTabClick(tab)"
      >
        <slot :tab="tab" :active="isActive(tab)">
          <span class="relative">
            {{ tab.label }}
            <span
              v-if="tab.indicatorText"
              class="r-indicator"
              :class="`r-indicator--${tab.indicatorType || 'danger'}`"
            >
              {{ tab.indicatorText }}
            </span>
          </span>
          <UIcon v-if="tab.icon" v-bind="iconBind(tab.icon)" />
        </slot>
      </NuxtLink>

      <a
        v-else
        href="#"
        class="tab"
        :class="{ active: props.active === tab.key || tab.active }"
        @click="onTabClick(tab)"
      >
        <slot :tab="tab" :active="isActive(tab)">
          <span class="relative">
            {{ tab.label }}
            <span
              v-if="tab.indicatorText"
              class="r-indicator"
              :class="`r-indicator--${tab.indicatorType || 'danger'}`"
            >
              {{ tab.indicatorText }}
            </span>
          </span>
          <UIcon v-if="tab.icon" v-bind="iconBind(tab.icon)" />
        </slot>
      </a>
    </li>
    <slot name="end" />
  </ul>
</template>

<style lang="scss">
.tabs-list {
  @apply flex text-sm font-medium text-center text-neutral-light-800 dark:text-neutral-dark-200;
}
.tabs-list.tabs-list-filled {
  @apply p-1.5 bg-neutral-light-200 dark:bg-neutral-dark-700 text-black dark:text-white rounded gap-1.5;
}
.tabs-list.tabs-list-filled .tab {
  @apply px-8 rounded border-none;
}
.tabs-list.tabs-list-filled .tab.active {
  @apply border-none bg-[var(--base-primary-color)] text-white font-bold;
}
.tab {
  @apply relative inline-flex items-center gap-3 px-6 py-2 rounded-t-lg border-b-2 dark:border-neutral-dark-500 border-neutral-light-200 hover:text-gray-600 hover:border-gray-300 dark:hover:text-gray-300 snap-center w-full justify-center;
}
.tab.active {
  @apply text-[var(--base-primary-color)] border-b-2 border-[var(--base-primary-color)] dark:text-[var(--base-primary-color)] dark:border-[var(--base-primary-color)];
}
.tab.disabled {
  @apply opacity-50 cursor-not-allowed;
}
.r-indicator {
  @apply absolute z-10 -top-2 -right-3 h-3.5 min-w-[14px] px-1 rounded-full text-[10px] leading-[14px] text-white;
}
.r-indicator.r-indicator--warning {
  @apply bg-[#F18F01] dark:bg-[#F18F01];
}
.r-indicator.r-indicator--danger {
  @apply bg-[#E13100] dark:bg-[#E13100];
}
</style>
